// src/services/api.ts
import axios from 'axios';
import {
  ChatMessageDTO,
  ChatApiResponse,
  LoginDTO,
  RegisterDTO,
  EventRegistrationDTO
} from '@/types/index';

const api = axios.create({
  baseURL: 'http://localhost:3000',
  withCredentials: true
});

api.interceptors.request.use(config => {
  if (typeof window !== 'undefined') {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers = config.headers || {};
      config.headers.Authorization = `Bearer ${token}`;
    }
  }
  return config;
});

/* --------- AUTH --------- */

export const login = async (payload: LoginDTO) => {
  try {
    const { data } = await api.post('/api/auth/login', payload);
    return data;
  } catch (error: any) {
    console.error(
      'LOGIN ERROR:',
      error?.message,
      error?.response?.status,
      error?.response?.data
    );
    throw error;
  }
};

export const register = async (payload: RegisterDTO) => {
  try {
    const { data } = await api.post('/api/auth/register', payload);
    return data;
  } catch (error: any) {
    console.error(
      'REGISTER ERROR:',
      error?.message,
      error?.response?.status,
      error?.response?.data
    );
    throw error;
  }
};

/* --------- CHAT / AI --------- */

export const chatWithAI = async (
  payload: ChatMessageDTO
): Promise<ChatApiResponse> => {
  const { data } = await api.post<ChatApiResponse>('/api/ai/chat', payload);
  return data;
};

/* --------- EVENT REGISTRATION (exemplo) --------- */

export const saveEventRegistration = async (
  payload: EventRegistrationDTO
) => {
  const { data } = await api.post('/api/events/registration', payload);
  return data;
};

export default api;
